import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Loader2 } from 'lucide-react';
import { toast } from "sonner";
import React from 'react';

type Card = {
  id: number;
  emoji: string;
  isFlipped: boolean;
  isMatched: boolean;
};

const EMOJIS = ['', '', '', ''];
const TIME_LIMIT = 30;

const Card2D = ({ card, onClick , key}: { card: Card; onClick: () => void, key: number}) => {
  return (
    <motion.div
      className={`w-24 h-32 cursor-pointer rounded-lg flex items-center justify-center text-3xl 
        ${card.isMatched ? 'bg-game-success' : card.isFlipped ? 'bg-game-accent' : 'bg-emerald-950'}`}
      onClick={onClick}
      whileHover={{ scale: 1.05 }}
      whileTap={{ scale: 0.95 }}
      animate={{ 
        rotateY: card.isFlipped || card.isMatched ? 180 : 0,
        backgroundColor: card.isMatched ? '#22C55E' : card.isFlipped ? '#8B5CF6' : '#cccccc'
      }}
      transition={{ duration: 0.3 }}
    >
      {/* Le div avec l'emoji est maintenant toujours cach sauf si la carte est retourne (isFlipped) ou apparie (isMatched) */}
      <div style={{ display: card.isFlipped || card.isMatched ? 'block' : 'none' }}>
        {card.emoji}
      </div>

    </motion.div>
  );
};

export const MemoryGameCaptcha = () => {
  const [cards, setCards] = useState<Card[]>([]);
  const [flippedCards, setFlippedCards] = useState<number[]>([]);
  const [timeLeft, setTimeLeft] = useState(TIME_LIMIT);
  const [gameStarted, setGameStarted] = useState(false);
  const [matchedPairs, setMatchedPairs] = useState(0);

  useEffect(() => {
    initializeGame();
  }, []);

  useEffect(() => {
    if (gameStarted && timeLeft > 0) {
      const timer = setInterval(() => {
        setTimeLeft((prev) => prev - 1);
      }, 1000);
      return () => clearInterval(timer);
    } else if (timeLeft === 0 && gameStarted) {
      toast.error("Time's up! Try again!");
    }
  }, [timeLeft, gameStarted]);

  useEffect(() => {
    if (matchedPairs === EMOJIS.length) {
      toast.success("Great job! CAPTCHA validated!", {duration: 1000});
    }
  }, [matchedPairs]);

  const initializeGame = () => {
    const duplicatedEmojis = [...EMOJIS, ...EMOJIS];
    const shuffledEmojis = duplicatedEmojis.sort(() => Math.random() - 0.5);
    
    const newCards: Card[] = shuffledEmojis.map((emoji, index) => ({
      id: index,
      emoji,
      isFlipped: false,
      isMatched: false
    }));
    
    setCards(newCards);
    setFlippedCards([]);
    setMatchedPairs(0);
    setTimeLeft(TIME_LIMIT);
    setGameStarted(false);
  };

  const handleCardClick = (cardId: number) => {
    if (!gameStarted) {
      setGameStarted(true);
    }

    if (flippedCards.length === 2 || cards[cardId].isMatched || flippedCards.includes(cardId)) {
      return;
    }

    if (timeLeft === 0) return; 

    const newCards = [...cards];
    newCards[cardId].isFlipped = true;
    setCards(newCards);

    const newFlippedCards = [...flippedCards, cardId];
    setFlippedCards(newFlippedCards);

    if (newFlippedCards.length === 2) {
      const [firstCard, secondCard] = newFlippedCards;
      if (cards[firstCard].emoji === cards[secondCard].emoji) {
        setTimeout(() => {
          const matchedCards = [...cards];
          matchedCards[firstCard].isMatched = true;
          matchedCards[secondCard].isMatched = true;
          setCards(matchedCards);
          setMatchedPairs((prev) => prev + 1);
          setFlippedCards([]);
          toast.success("Match found! ", { duration: 1000});
        }, 500);
      } else {
        setTimeout(() => {
          const unflippedCards = [...cards];
          unflippedCards[firstCard].isFlipped = false;
          unflippedCards[secondCard].isFlipped = false;
          const sEmoji = unflippedCards[secondCard].emoji;
          unflippedCards[secondCard].emoji = unflippedCards[firstCard].emoji;
          unflippedCards[firstCard].emoji = sEmoji;
          setCards(unflippedCards);
          setFlippedCards([]);
          toast("Try again!", { duration: 1000 });
        }, 1000);
      }
    }
  };

  return (
    <div className="w-full max-w-md mx-auto bg-gray-50">
      <div className="mb-4 flex justify-between items-center">
        <span>Veuillez choisir toutes les paires pour valider le CAPTCHA. Attention, il y a un lment de surprise!</span>
        <span className="text-xl font-bold">Temps: {timeLeft}s</span>
        <span className="text-xl font-bold">Paires: {matchedPairs}/{EMOJIS.length}</span>
      </div>
      
      <motion.div 
        className="grid grid-cols-4 gap-4 p-4 bg-game-background rounded-lg"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        {cards.map((card: Card) => (
          <Card2D
            key={card.id}
            card={card}
            onClick={() => handleCardClick(card.id)}
          />
        ))}
      </motion.div>

      {timeLeft === 0 && (
        <motion.button
          onClick={initializeGame}
          className="mt-4 w-full bg-game-accent hover:bg-opacity-80 text-red-800 py-2 rounded-lg flex items-center justify-center gap-2"
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
        >
          Veuillez ressayer!
        </motion.button>
      )}
      {(matchedPairs === EMOJIS.length) && <motion.button
          onClick={initializeGame}
          className="mt-4 w-full bg-game-accent hover:bg-opacity-80 text-green-800 py-2 rounded-lg flex items-center justify-center gap-2"
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
        >
          CAPTCHA Valid!
        </motion.button>}
    </div>
  );
};